import React from "react";
import * as Icon from "react-bootstrap-icons";
import { CosLoggrLogo, CosLoggrText } from "../icons/index";

export default function Footer() {
  return (
    <section className="section footer">
      <div className="app-logo">
        <div className="logo">
          <CosLoggrLogo className="logo-icon" />
          <CosLoggrText className="logo-type" />
        </div>
        <h5>All rights reserved &copy; {new Date().getFullYear()}</h5>
      </div>
      <div className="topic">
        <h3 className="title">Developer</h3>
        {/* <span>Raff Julius O. Martinez</span> */}
        <a href="/developerContact">
          <span>
            <Icon.EnvelopeFill /> arjayem.developer@gmail.com
          </span>
        </a>
      </div>
      {/* <div className="topic"></div> */}
    </section>
  );
}
