import React, { useState, useEffect } from "react";
import { auth, usersCollection } from "../firebase";
import { useNavigate } from "react-router-dom";

export default function Wizard() {
  const [error, setError] = useState("");
  const [message, setMessage] = useState("");
  const [loading, setLoading] = useState(false);

  let navigate = useNavigate();

  const [cosplayName, setCosplayName] = useState(
    () => auth.currentUser.displayName
  );
  const [isValidCosplayName, setIsValidCosplayName] = useState(() => false);

  useEffect(() => {
    //console.log(isValidCosplayName.toString(), cosplayName);
    checkCosplayNameValidity();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [cosplayName]);

  function onCosplayNameKeyUp(e) {
    e.preventDefault();
    setCosplayName(e.currentTarget.value);
  }

  function checkCosplayNameValidity() {
    var isValid =
      /^[A-Za-z0-9_@./#&+-]*$/g.test(cosplayName) && cosplayName.length !== 0;
    setIsValidCosplayName(isValid);
  }

  async function onNextClick(e) {
    load();

    e.preventDefault();
    console.log("next");
    var doc = usersCollection.doc(auth.currentUser.uid);
    await doc
      .update({
        cosplayName: cosplayName,
        isLoggedIn: true,
      })
      .finally(() => {
        navigate("/dashboard", { replace: true });
      })
      .catch((e) => {
        catchError(e, "update-cosplayname-error.");
      });
  }

  function load() {
    setMessage("");
    setError("");
    setLoading(true);
  }

  function catchError(e, msg) {
    setLoading(false);
    console.error(e.message);
    return setError(msg);
  }

  return (
    <>
      <div id="Wizard" className="container">
        <div className="card">
          <div className="card-title">
            <h2>Enter Cosplay Name</h2>
          </div>
          Only special characters allowed are hypens(-), underscores(_) and
          dots(.).
          <form>
            <input
              type="text"
              className="form-control wide"
              defaultValue={cosplayName}
              onKeyUp={(e) => onCosplayNameKeyUp(e)}
              onChange={(e) => onCosplayNameKeyUp(e)}
            />
            <button
              type="button"
              className="btn btn-primary"
              disabled={!isValidCosplayName}
              onClick={(e) => onNextClick(e)}
            >
              Next
            </button>
          </form>
        </div>
      </div>
    </>
  );
}
