import React, { useEffect } from "react";
import { isMobile } from "react-device-detect";
import { AuthProvider } from "./context/AuthContext";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";

import Home from "./home/Home";
// import SignIn from "./app/SignIn";
// import CreateYourBlog from "./app/CreateYourBlog";
import DeveloperContact from "./app/DeveloperContact";
import PrivateRoute from "./app/PrivateRoute";
import UserDashboard from "./user/Dashboard";
import BlogDashboard from "./blog/Dashboard";
import Profile from "./user/Profile";
import Wizard from "./user/Wizard";

function App() {
  useEffect(() => {
    if (isMobile) {
      document.getElementById("root").classList.add("mobile");
    }

    window.addEventListener("scroll", function () {
      var scrollTop = window.pageYOffset || document.documentElement.scrollTop;
      var clientHeight = document.querySelector(".section").clientHeight;
      //var scrollHeight = document.documentElement.scrollHeight; // / 4;

      if (scrollTop > clientHeight / 2) {
        document.querySelector(".navigation").classList.add("sticky");
      } else {
        document.querySelector(".navigation").classList.remove("sticky");
      }

      var headers = Array.from(
        document.querySelectorAll(".section header:not(first-child)")
      );
      headers.forEach((header, i) => {
        if (header) {
          var position = header.getBoundingClientRect();
          if (position.top >= 0 && position.bottom <= window.innerHeight) {
            header.classList.add("slide");
          } else {
            // if (header.classList.length > 0) {
            //   header.classList.remove("slide");
            // }
          }
        }
      });
    });
  }, []);

  useEffect(() => {
    let vh = window.innerHeight * 0.01;
    document.documentElement.style.setProperty("--vh", `${vh}px`);
  }, []);

  return (
    <>
      <Router>
        <AuthProvider>
          <Routes>
            {/* 
            <Route exact path="/signin" element={<SignIn />} />
            <Route exact path="/navigation" element={<Navigation />} />
            <Route exact path="/createyourblog" element={<CreateYourBlog />} /> */}
            <Route
              exact
              path="/developerContact"
              element={<DeveloperContact />}
            />
            <Route exact path="/enterCosplayName" element={<Wizard />} />

            <Route path="/blog" element={<PrivateRoute />}>
              <Route exact path="/blog:username" element={<BlogDashboard />} />
            </Route>
            <Route path="/profile" element={<PrivateRoute />}>
              <Route exact path="/profile/:id" element={<Profile />} />
            </Route>

            <Route exact path="/blog" element={<BlogDashboard />} />
            <Route exact path="/dashboard" element={<UserDashboard />} />

            <Route path="/" element={<Home />} />
          </Routes>
        </AuthProvider>
      </Router>
    </>
  );
}

export default App;
