import React from "react";
import Navigation from "./Navigation";
import Footer from "./Footer";

const Home = () => {
  return (
    <>
      <Navigation />
      <section className="section center">
        <header>
          <h1>Bring out your passion at cosplaying</h1>
          <p>Create a collection and portfolio of your journey.</p>
          <br />
          <a
            href="/createyourblog"
            className="btn btn-primary"
            onClick={function (e) {
              e.preventDefault();
              document
                .querySelector(
                  ".navigation .header-buttons a[href='/createyourblog']"
                )
                .click();
            }}
          >
            Create Your Blog
          </a>
        </header>
      </section>
      <section className="section left">
        <header>
          <h1>Introduce Yourself</h1>
          <p>
            Let everyone know a little something about you, how you started and
            where you are right now.
          </p>
        </header>
      </section>
      <section className="section right">
        <header>
          <h1>Your Journey</h1>
          <p>
            Create a collection of events you joined and share your experience.
          </p>
        </header>
      </section>
      <section className="section center">
        <header>
          <h1>Join Now</h1>
          <p>
            Whether you're an amateur or professional, sharing your experience
            and insights would still inspire newbies to start their own journey.
            Publish your passion and start inspiring others now.
          </p>
          <br />
          <a
            href="/createyourblog"
            className="btn btn-primary dark"
            onClick={function (e) {
              e.preventDefault();
              document
                .querySelector(
                  ".navigation .header-buttons a[href='/createyourblog']"
                )
                .click();
            }}
          >
            Create Your Blog
          </a>
        </header>
      </section>
      <Footer />
    </>
  );
};

export default Home;
