/* eslint-disable */
import { useState } from "react";
import { isMobile } from "react-device-detect";
import { useNavigate } from "react-router-dom";
import { auth, googleProvider, usersCollection } from "../firebase";
import { CosLoggrLogo, CosLoggrText } from "../icons/index";

export default function Navigation() {
  const [error, setError] = useState("");
  const [loading, setLoading] = useState(false);

  let navigate = useNavigate();

  async function handleOnClick(e, provider) {
    e.preventDefault();

    if (provider == null) {
      return console.log("Invalid provider.");
    }

    try {
      await auth.signInWithPopup(provider);

      var doc = usersCollection.doc(auth.currentUser.uid);

      if (!(await doc.get()).exists) {
        await doc
          .set({
            uid: auth.currentUser.uid,
            //createdDate: auth.currentUser.metadata.createdDate ?? Date.now(),
            //displayName: auth.currentUser.displayName ?? auth.currentUser.email,
          })
          .catch((e) => {
            catchError(e, "set-user-error.");
          });
      }

      await doc
        .update({
          lastLogIn: auth.currentUser.metadata.lastSignInTime,
          providerData: auth.currentUser.providerData.map((e) => e)[0],
          //isLoggedIn: true,
        })
        .catch((e) => {
          catchError(e, "update-user-error.");
        });

      await usersCollection
        .doc(auth.currentUser.uid)
        .get()
        .then((snapshot) => {
          if (snapshot.exists) {
            if (snapshot.data().cosplayName || "") {
              navigate("/dashboard", { replace: true });
            } else {
              navigate("/enterCosplayName", { replace: true });
            }
          }
        });
    } catch (e) {
      catchError(e);
    }
  }

  function catchError(e, msg) {
    setLoading(false);
    console.error(e.message);
    return setError(msg);
  }

  return (
    <div className="navigation">
      <nav>
        <div className="logo">
          <a href="/">
            <CosLoggrLogo className="logo-icon" />
            {!isMobile && <CosLoggrText className="logo-type" />}
          </a>
        </div>
        <div className="header-buttons">
          <a
            href="/createyourblog"
            className="btn btn-primary"
            onClick={(e) => handleOnClick(e, googleProvider)}
          >
            Create Your Blog
          </a>
          <a
            href="/sigin"
            className="btn btn-secondary"
            onClick={(e) => handleOnClick(e, googleProvider)}
          >
            Sign In
          </a>
        </div>
      </nav>
    </div>
  );
}
